import React from "react";
import { useSelector, useDispatch } from "react-redux"; 

import { selectTempData, setCKEditorContent } from "../features/temp/tempSlice";

import CKEditor from '@ckeditor/ckeditor5-react';
//import ClassicEditor from '@ckeditor/ckeditor5-build-classic';
import BalloonEditor from '@ckeditor/ckeditor5-build-balloon-block';

export default function HTMLEditorBallonBlockLazy({id=0, setHtmlfeedback}) {
    const dispatch = useDispatch();
    const initContent = useSelector(selectTempData).ckeditor_node_content;
    const ckeditor_node_id = useSelector(selectTempData).ckeditor_node_content;

    if (id === ckeditor_node_id) { 

    return <CKEditor
                editor={BalloonEditor}
                data={!!initContent ? initContent :"<p>Комментарий</p>"}
           //     data={!!props.htmlfeedback ? props.htmlfeedback :"<p>Комментарий</p>"}
                onInit={editor => {
                    // You can store the "editor" and use when it is needed.
                   // console.log('Editor is ready to use!', editor);
                }}
                onChange={(event, editor) => {
                 //   const data = editor.getData();
                 //   console.log({ event, editor, data });
                 dispatch(setCKEditorContent(editor.getData()));
                }}
                onBlur={(event, editor) => {
                    setHtmlfeedback(editor.getData());
                }}
                onFocus={(event, editor) => {
             //       console.log('Focus.', editor);
                }}
            />
        }
   
        return null
}

